import modelExtend from 'dva-model-extend'
import { queryUserProfile, updataUserProfile } from '../../services/signup'
import { pageModel } from '../../models/common'
// import { message } from 'antd'
import queryString from 'query-string'
// import { message } from 'antd'

interface Istate {
  currentItem: any,
  modalVisible: boolean,
  detailVisible: boolean,
  user: any,
  detail: any
}
export default modelExtend(pageModel, {
  namespace: 'areadetail',
  state: {
    currentItem: {},
    modalVisible: false,
    detailVisible: false,
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/signup/areadetail') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put }: any) {
      const data = yield call(queryUserProfile, payload)
      if (data.success) {
        const item = data.data
        yield put({
          type: 'querySuccess',
          payload: {
            list: item.user_profile.data,
            pagination: {
              current: Number(payload.current_page) || 1,
              pageSize: Number(payload.per_page) || 10,
              total: item.user_profile.total,
            },
          },
        })
      } else {
        throw data
      }
    },
    * queryUpataUserProfile ({ payload }: any, { call, put }: any) {
      const data = yield call(updataUserProfile, payload)
      if (data.success) {
        yield put({
          type: 'query',
          payload: {
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
    updateDetailState (state: Istate, { payload }: any) {
      // let detail = state.detail
      return {
        ...state,
        detail: {
          ...state.detail,
          ...payload,
        },
      }
    },
    updateUserState (state: Istate, { payload }: any) {
      // let detail = state.detail
      return {
        ...state,
        user: {
          ...state.user,
          ...payload,
        },
      }
    },
  },
})



