import { getLocalStorage } from '../utils/utils'
import config from '../utils/config'
import request from '../utils/request'

const { api } = config
const {
  topSongList,
  regprofile,
  reglist,
  updateReg,
  userProfile,
} = api

export function queryTopSongList (params: any) {
  return request({
    url: topSongList,
    method: 'get',
    data: params,
  })
}
export function queryRegprofile (params: any) {
  return request({
    url: regprofile,
    method: 'get',
    data: params,
  })
}
export function queryReglist (params: any) {
  return request({
    url: reglist,
    method: 'get',
    data: params,
  })
}

export function queryUpdateReg (params: any) {
  const SystemAdmin = getLocalStorage('SystemAdmin')
  const url = updateReg.replace(':uid', SystemAdmin.id)
  return request({
    url: url.replace(':id', params.id),
    method: 'put',
    data: params,
  })
}

export function queryUserProfile (params: any) {
  return request({
    url: userProfile,
    method: 'get',
    data: params,
  })
}

export function updataUserProfile (params: any) {
  return request({
    url: `${userProfile}/${params._id}`,
    method: 'put',
    data: params,
  })
}



